<template>
  <div class="col-12 col-md-10 mx-auto">
    <table class="table">
      <thead>
        <tr>
          <th>Name</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(comic, id) in comics" :key="id">
          <td class="">
            {{comic.name}}
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      comics: [],
    };
  },
  mounted() {
    axios
      .post("http://54.215.233.125/api", {
        query: `
      query {
        getComicsByHeroId(id: "${this.$route.params.id}"){
          items {
            name
          }
        }
      }`,
      })
      .then((result) => {
        this.comics = result.data.data.getComicsByHeroId[0].items;
      })
      .catch((err) => console.log(err.response));
  },
};
</script>
