<template>
    <div class="col-12 col-md-10 mx-auto">
        <table class="table text-left">
            <thead>
                <tr>
                    <th>Name</th>
                    <th># of Comics</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="hero in displayedHeroes" :key="hero.id">
                    <td><router-link :to="{ name: 'comics', params: {id: hero.id}}">{{hero.name}}</router-link></td>
                    <td>{{hero.comics[0].available}}</td>
                </tr>
            </tbody>
        </table>
        <button v-if="!loading" :disabled="this.page < 1" class="col-6 btn btn-secondary" @click="gotoPrevPage">&#8592;</button>
        <button v-if="!loading" :disabled="this.page === numPages" class="col-6 btn btn-primary" @click="gotoNextPage">&#8594;</button>
        <socket v-if="loading" class="mx-auto"></socket>
    </div>
</template>

<script>
import {Socket} from 'vue-loading-spinner'
import axios from "axios";

export default {
    name:'HeroList',
    components: {
        Socket
    },
    data(){
        return {
            heroes: [],
            loading: true,
            page:0
        }
    },
    computed: {
        displayedHeroes: function() {
            console.log(this.startIndex);
            return this.heroes.slice(this.startIndex, this.endIndex);
        },
        startIndex: function(){
            return this.page * 10;
        },
        endIndex: function(){
            return this.startIndex + 9;
        },
        numPages: function(){
            return Math.floor( this.heroes.length/10 )
        }
    },
    mounted() {
        axios
        .post("http://54.215.233.125/api", {
            query: `
            query {
                getHeroes {
                    id
                    name
                    comics {
                        available
                    }
                }
            }`
            }
        )
        .then( result => {
            this.loading = false;
            this.heroes = result.data.data.getHeroes;
        })
        .catch( err => console.log(err.response));
    },
    methods: {
        gotoNextPage: function(){
            if(this.page < this.numPages){
                this.page++;
            }
        },
        gotoPrevPage: function(){
            if (this.page > 0){
                this.page--;
            }
        }
    }
}
</script>