import VueRouter from 'vue-router'
import HeroList from '../components/HeroList'
import Comics from '../components/Comics'

const routes = [
  {
    path: '/',
    name: 'app',
    component: HeroList
  },
  {
    path: '/:id',
    name: 'comics',
    component: Comics
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router